import { ListItem, ListItemIcon, ListItemText,styled } from '@mui/material'
import React from 'react'
import DeleteIcon from '@mui/icons-material/Delete';

const Detail=styled(ListItem)`
margin-top:10px;
`
const Transection= ({transection,setTransections,transections}) => {

  const deleteTransection=(id)=>{
    setTransections(transections.filter(transection=>transection.id!==id))
  }

  const color=transection.amount>0 ? 'Green':'Red';
return(
  <Detail style={{background: `${color}`,color:'#fff'}}>
    <ListItemIcon>
      <DeleteIcon onClick={()=>deleteTransection(transection.id)}/>
    </ListItemIcon>
    <ListItemText>{transection.text}</ListItemText>
    <ListItemText>{transection.amount}</ListItemText>
  </Detail>
)
}
export default Transection;