import React from 'react'
import { Box,Button,TextField,Typography ,styled} from '@mui/material';
import { useState } from 'react';


const Container=styled(Box)`
display:flex;
flex-direction:column;
& >h5 ,&>div,&>Button{
  margin-top:30px;
  
} `
 const NewTransections=({setTransections})=> {
  const [text,setText]= useState('');
  const [amount,setAmount]=useState ();


  const addTransections=()=>{
    const transection={
      id:Math.floor(Math.random()*100000),
      text:text,
      amount: +amount
    }
    setTransections(prevState=>[transection,...prevState]);

  }
  return (
  <Container>
    <Typography variant='h5'>New Transection</Typography>
    <TextField label='Enter expenses' onChange={(e)=> setText(e.target.value)}/>
    <TextField label='Enter Amount' onChange={(e)=> setAmount(e.target.value)}/>
    <Button variant='contained' onClick={()=>addTransections()}>Add Transections</Button>
  </Container>
  )
}
export default NewTransections;